import React, { useState, useEffect } from 'react'
import Note from './components/Note'
import noteService from './services/notes'
import Notification from './components/Notification'
const App = () => {
  const [notes, setNotes] = useState([]) 
  const [newNote, setNewNote] = useState('') 
  const [showAll, setShowAll] = useState(true)
  const [errorMessage, setErrorMessage] = useState('');

  useEffect(() => {
    noteService.getAll()
      .then(initnotes => {
        setNotes(initnotes)
      })
  }, [])


  const toggleImportanceOf = (id) => {
    const note = notes.find(n => n.id === id) /* find a reference to the associated note */
    const changedNote = { ...note, important: !note.important }
    noteService.update(id, changedNote).then(note_new => 
      /* replace notes database with original, expect specific one modified */
      setNotes(notes.map(note => note.id !== id ? note : note_new))
    ).catch(() => {
      setErrorMessage(`${note.message} was already removed`);
      setTimeout(() => { setErrorMessage("") }, 1000);
    })
  }

  const notesToShow = showAll
    ? notes
    : notes.filter(note => note.important)

  const rows = () => notesToShow.map(note =>
    <Note
      key={note.id}
      note={note}
      toggleImportance={() => toggleImportanceOf(note.id)}
    />
  )
  
  const handleNoteChange = (event) => {
    setNewNote(event.target.value)
  }

  const addNote = (event) => {
    event.preventDefault()
    const noteObject = {
      content: newNote,
      date: new Date().toISOString(),
      important: Math.random() > 0.5,
      id: notes.length + 1,
    }

    noteService.create(noteObject)
      .then(new_note => {
        setNotes(notes.concat(new_note))
        setNewNote('')
      }).catch((reason) => console.log(reason))
  }

  return (
    <div>
      <h1>Notes</h1>
      <Notification message={errorMessage} />
      <div>
        <button onClick={() => setShowAll(!showAll)}>
          show {showAll ? 'important' : 'all'}
        </button>
      </div>
      <ul>
        {rows()}
      </ul>
      <form onSubmit={addNote}>
        <input
          value={newNote} 
          onChange={handleNoteChange}
        />
        <button type="submit">save</button>
      </form>
    </div>
  )
}

export default App 